@import url(https://fonts.googleapis.com/css?family=Raleway:600,800&display=swap);
@import url(https://fonts.googleapis.com/css?family=Raleway:400,800&display=swap);
@charset "UTF-8";

* {
    margin:  0;
    border:  0;
    padding: 0;
    outline: 0;

    list-style: none;
    text-decoration: none;
    text-rendering: optimizeLegibility;

    box-sizing: border-box;

    -webkit-webkit-font-smoothing: subpixel-antialised;
       -moz-webkit-font-smoothing: subpixel-antialised;
        -ms-webkit-font-smoothing: subpixel-antialised;
         -o-webkit-font-smoothing: subpixel-antialised;
}

body {
    height: 100vh;
}

:root {
    --accent: #000;
}

body {
    background: -webkit-gradient(linear, left bottom, left top, color-stop(-100%, #7a6aff), color-stop(60%, #fff));
    background: linear-gradient(to top, #7a6aff -100%, #fff 60%);
}

#card {
    position: absolute;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: auto;
    right: 0;
    left: 0;
    top: 0;
    bottom: 0;
    width: 300px;
    height: 300px;
    background: rgba(255, 255, 255, 0.6);
    border-radius: 12%;
    box-shadow: 0 30px 50px rgba(0, 0, 0, .05);
    -webkit-transition: all .5s ease-in-out;
    transition: all .5s ease-in-out;
}

#card.fullscreen {
    width: 800px;
    height: 100%;
}

#card .info {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Raleway', sans-serif;
    margin-top: 3em;
}

#card .info .fullname {
    font-size: 12pt;
    font-weight: 800;
    color: #51576b;
}

#card .info .surname {
    font-weight: 600;
}

#card .info .role {
    font-size: 9pt;
    font-weight: 500;
    line-height: 3;
    color: #7c839a;
}

#card .more {
    border-radius: 30%;
}

#card .more {
    position: absolute;
    bottom: -20px;
    background: black;
    width: 40px;
    height: 40px;
    cursor: pointer;
    background-color: #47485a;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 10px 20px rgba(0, 0, 0, .3);
    -webkit-transition: background-color .3s, all .6s;
    transition: background-color .3s, all .6s;
    -webkit-animation: bounce 3s infinite ease-in-out;
            animation: bounce 3s infinite ease-in-out;
}

#card .more:not(.expanded):hover {
    background-color: #575871;
}

#card .more.expanded {
    -webkit-animation: none;
            animation: none;
    background-image: none;
    cursor: default;
    width: 100%;
    bottom: 0;
    border-radius: 30px;
    box-shadow: none;
    overflow: hidden;
    -webkit-animation: expand-info .5s forwards linear;
            animation: expand-info .5s forwards linear;
    -webkit-animation-delay: .5s;
            animation-delay: .5s;
}

#card .more.expanded .dots {
    position: absolute;
    width: 100%;
    height: 100%;
}

#card .more.expanded .more-info {
    z-index: 1;
}

#card .more-info {
    width: 100%;
    height: 100%;
}

#card .paginator {
    width: 100%;
    height: 100%;
    display: inline-grid;
    align-items: center;
    grid-template-rows: [back] 80px [content] auto [menu] 60px;
}

#card .paginator .content {
    padding: 0 1.8em 2em 1.8em;
    height: 100%;
}

#card .paginator .go-back {
    --asset-back: url(/homepage/static/media/back.8d350d84.png);
    background: var(--asset-back) center no-repeat;
    width: 9px;
    height: 14px;
    grid-row: back;
    cursor: pointer;
    padding: 1em;
    margin-left: 20px;
    -webkit-filter: opacity(.45);
            filter: opacity(.45);
    -webkit-transition: -webkit-filter .2s ease-in-out;
    transition: -webkit-filter .2s ease-in-out;
    transition: filter .2s ease-in-out;
    transition: filter .2s ease-in-out, -webkit-filter .2s ease-in-out;
}

#card .paginator .go-back:hover {
    -webkit-filter: opacity(1);
            filter: opacity(1);
}

#card .more-info .content {
    grid-row: content;
}

#card .more-info #menu {
    grid-row: menu;
}

#card .more.expanded .dots div {
    --position: -300px;
    --delay: .6s;
    margin: 0;
    bottom: var(--position);
    background: rgba(36, 39, 56, 0.5);
    -webkit-animation: transition-expand-more var(--delay) forwards linear;
            animation: transition-expand-more var(--delay) forwards linear;
    -webkit-animation-delay: var(--delay);
            animation-delay: var(--delay);
    border-radius: 0;
    width: 100%;
    height: 100%;
    position: absolute;
}

#card .more.expanded .dots div:nth-child(1) {
    background: #af453b;
}

#card .more.expanded .dots div:nth-child(2) {
    --delay: .7s;
    background: #47a579;
}

#card .more.expanded .dots div:nth-child(3) {
    --delay: .8s;
    background: #3b3c42;
}

#card .more.retract {
    -webkit-animation: retract .5s forwards linear;
            animation: retract .5s forwards linear;
}

#card .more .dots.hidden {
    visiblity: hidden;
    display: none;
}

#card .more .dots {
    display: flex;
    align-items: center;
    justify-content: center;
}

#card .more .dots div {
    width: 4px;
    height: 4px;
    background: #fff;
    margin: 2px;
    border-radius: 50%;
    -webkit-transition: all .3s;
    transition: all .3s;
}

#card .more:not(.expanded) .more-info {
    visibility: hidden;
    display: none;
}

#card .more:not(.expanded):hover .dots div {
    -webkit-animation: bounce-dots 1s infinite linear;
            animation: bounce-dots 1s infinite linear;
}

#card .more:not(.expanded) .dots div:nth-child(1) {
    -webkit-animation-delay: 0s;
            animation-delay: 0s;
}

#card .more:not(.expanded) .dots div:nth-child(2) {
    -webkit-animation-delay: .05s;
            animation-delay: .05s;
}

#card .more:not(.expanded) .dots div:nth-child(3) {
    -webkit-animation-delay: .08s;
            animation-delay: .08s;
}

@-webkit-keyframes bounce {
    50% {
        bottom: -10px;
    }
}

@keyframes bounce {
    50% {
        bottom: -10px;
    }
}

@-webkit-keyframes bounce-dots {
    45% {
        margin-top: 10px;
    }
    75% {
        margin-top: -10px;
    }
}

@keyframes bounce-dots {
    45% {
        margin-top: 10px;
    }
    75% {
        margin-top: -10px;
    }
}

@-webkit-keyframes expand-info {
    from {
        height: 40px;
    }
    to {
        height: 100%;
    }
}

@keyframes expand-info {
    from {
        height: 40px;
    }
    to {
        height: 100%;
    }
}

@-webkit-keyframes retract {
    from {
        height: 100%;
    }
    to {
        height: 40px;
    }
}

@keyframes retract {
    from {
        height: 100%;
    }
    to {
        height: 40px;
    }
}

@-webkit-keyframes transition-expand-more {
    from {
        bottom: -300px;
    }
    to {
        bottom: 0;
    }
}

@keyframes transition-expand-more {
    from {
        bottom: -300px;
    }
    to {
        bottom: 0;
    }
}

:root {
    --asset-profile: url(/homepage/static/media/profile.1df5c879.png);
    --asset-projects: url(/homepage/static/media/projects.f9d6410b.png);
    --asset-github: url(/homepage/static/media/github.c6694568.png);
    /* --asset-dotfiles: url('../../assets/dotfiles.png'); */
}

#menu {
    position: relative;
    width: 100%;
    height: 60px;
    background: rgb(79, 80, 85);
    border-radius: 30px 0;
}

#menu .sections {
    display: inline-grid;
    grid-template-columns: repeat(4, .5fr);
    width: 100%;
    height: 100%;
    padding-left: .8em;
}

#menu .section {
    cursor: pointer;
    border-radius: 20px;
    -webkit-transition: -webkit-filter .1s ease-in-out;
    transition: -webkit-filter .1s ease-in-out;
    transition: filter .1s ease-in-out;
    transition: filter .1s ease-in-out, -webkit-filter .1s ease-in-out;
}

#menu .section:not(.github) {
    -webkit-filter: opacity(.3);
            filter: opacity(.3);
}

#menu .mark {
    --blur: 12px;
    --spread: 2px;
    position: absolute;
    width: 8px;
    height: 5px;
    background: #fff;
    border-radius: 50%;
    left: 45px;
    bottom: -2px;
    box-shadow: 0 -2px var(--blur) var(--spread) #fff;
    -webkit-transition: left .2s ease-in-out;
    transition: left .2s ease-in-out;
}

#menu .section.active ~ .mark {
    -webkit-animation: shine .3s forwards linear;
            animation: shine .3s forwards linear;
}

#menu .section:hover {
    -webkit-filter: opacity(.7);
            filter: opacity(.7);
}

#menu .section.active {
    -webkit-filter: opacity(1);
            filter: opacity(1);
}

#menu .section.profile  { background: url(/homepage/static/media/profile.1df5c879.png) center no-repeat; background: var(--asset-profile) center no-repeat; }
#menu .section.projects { background: url(/homepage/static/media/projects.f9d6410b.png) center no-repeat; background: var(--asset-projects) center no-repeat; }
/* #menu .section.dotfiles { background: var(--asset-dotfiles) center no-repeat; } */

#menu .section.projects.active ~ .mark {
    left: 38%;
}

#menu .section.github {
    background: url(/homepage/static/media/github.c6694568.png) center no-repeat;
    background: var(--asset-github) center no-repeat;
    background-color: #bf4567;
    border-radius: 60px 0 0 60px;
}

#menu .section::after {
    content: attr(name);
    text-transform: uppercase;
    color: #838487;
    font: 600 9.9px 'Roboto', sans-serif;
    letter-spacing: 1.5px;
    text-align: center;
    display: block;
    position: relative;
    padding: .5em;
    top: 0;
    opacity: 0;
    -webkit-transition: all .25s ease-in;
    transition: all .25s ease-in;
    border-radius: 40px;
}

#menu .section:hover::after {
    top: -25px;
    opacity: 1;
}

@-webkit-keyframes shine {
    0% {
        --spread: 0;
    }
    50% {
        --spread: 15px;
        --blur: 30px;
    }
    100% {
        --spread: 2px;
        --blur: 12px;
    }
}

@keyframes shine {
    0% {
        --spread: 0;
    }
    50% {
        --spread: 15px;
        --blur: 30px;
    }
    100% {
        --spread: 2px;
        --blur: 12px;
    }
}

#profile {
    width: 100%;
    height: 100%;
    -webkit-animation: slide-in .5s forwards linear;
            animation: slide-in .5s forwards linear;
    -webkit-transition: -webkit-transform .5s ease-in-out;
    transition: -webkit-transform .5s ease-in-out;
    transition: transform .5s ease-in-out;
    transition: transform .5s ease-in-out, -webkit-transform .5s ease-in-out;
    font-family: 'Raleway', sans-serif;
}

#profile h1 {
    font-size: 18pt;
    font-weight: 800;
    color: #fff;
    margin-bottom: .4em;
}

#profile p {
    font-size: 11pt;
    color: #c0c3d8;
}

@-webkit-keyframes slide-in {
    from {
        -webkit-transform: translateY(100%);
                transform: translateY(100%);
    }
    to {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@keyframes slide-in {
    from {
        -webkit-transform: translateY(100%);
                transform: translateY(100%);
    }
    to {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

#card .avatar {
    max-width: 100px;
    max-height: 100px;
    overflow: hidden;
    box-shadow: 0 15px 30px rgba(0, 0, 0, .15);
    border-radius: 30%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #fff;
}

#card .avatar img {
    width: 100%;
}

.loader:not(.loaded) {
    display: none;
}

.loader {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    position: absolute;
    background: #fff;
    z-index: 1;
}

.loader::before,
.loader::after {
    content: '';
    position: absolute;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background: transparent;
}

.loader::before {
    border-top-left-radius: 50px;
    border-top-right-radius: 50px;
    border: 4px solid black;
    border-bottom: 0;
    border-right: 0;
    border-left: 0;
    -webkit-animation: rotate .5s infinite linear;
            animation: rotate .5s infinite linear;
}

.loader::after {
    box-shadow: 0 0 0 2px rgba(0, 0, 0, .1),
                inset 0 0 0 2px rgba(0, 0, 0, .1);
}

@-webkit-keyframes rotate {
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes rotate {
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

